import { Component, OnInit } from '@angular/core';
import { UserService } from '../../../shared/user.service';

import { MatDialog } from '@angular/material/dialog';

import { SignupComponent } from '../signup/signup.component';
import { LoginComponent } from '../login/login.component';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
  name = ""
  bn = ""

  // methana public krna eke aulak thiynawda danne naa, wenama service ekak haduwanm hri.. islog wage ewa danna
  constructor(public userService: UserService, public dialog: MatDialog) { }

  ngOnInit(): void {
    this.userService.islog = this.userService.isLoggedIn();

    this.getuser()
  }



  openDialog(): void {
    let dialogRef = this.dialog.open(SignupComponent);
    dialogRef.afterClosed().subscribe(() => {
  
    })
  }


  openDialoglogin(): void {
    let dialogRef = this.dialog.open(LoginComponent);
    dialogRef.afterClosed().subscribe(() => {
      this.ngOnInit()
    })
  }

  googlel() {

    window.open('http://localhost:3000/api/googlelll', "mywindow", "location=1,status=1,scrollbars=1, width=800,height=800");
    let listener = window.addEventListener('message', (message) => {
      //message will contain facebook user and details
    });



    console.log("gooooogle l B")
    // this.userService.googlelogin().subscribe(
    //  res=>{ console.log("ok "+res)},  
    //  err=>{console.log("errr"+JSON.stringify(err))}
    // )
  }

  getuser() {

    if(this.userService.isLoggedIn())
    this.userService.getUserProfile().subscribe(
      (res: any) => {

        this.name = res.user.fullName.toUpperCase();
        this.bn = res.user.fullName.split('')[0].toUpperCase();


      },
      err => {
        console.log(err)
      }
    );
  }

  logout() {
    this.userService.deleteToken()
    this.ngOnInit()
  }


}
